<template>
  <div class="trans">
    <div class="trans_container">
      <p class="txt">{{textArr[ind]}}</p>
      <div class="tag">
        <div class="btn_left" v-show="ind==3" @click="toStudy(5)"></div>
        <div class="btn_right" v-show="ind==3" @click="toStudy(4)"></div>
      </div>
    </div>
    <audio id="audio" class="Hidden" autoplay preload @ended="onEnd()" :src="audio_url" controls></audio>
    <div class="hv_tip">
      <div class="icon"></div>
      <p>请旋转至横屏，体验更佳</p>
    </div>
  </div>
</template>
<script scoped>
  import Eject from '../Eject'
  export default {
    name: 'Part',
    components: {
      eject: Eject,
    },
    data () {
      return {
        ind:0,
        id:0,
        num:2,
        ejectText:'',
        ejectStatus: false,
        textArr:['视频欣赏','绘本阅读','互动游戏','动画歌谣'],
        audio_url:''
      }
    },
    created(){
      // this.getInfo()
      // this.shareConfig()
    },
    mounted(){
      this.ind = this.$route.query.part-1 || 0;
      this.id = this.$route.query.id;
      this.audio_url = 'https://qnfile.abctime.com/extend/musical/trans_audio' + this.$route.query.part + '.mp3'
      wx.ready(function () {
        document.querySelector('#audio').play()
      })
    },
    methods: {
      back(){
        this.$router.replace({name:'Cata'})
      },
      onEnd(){
        if(this.ind == 3){

        }else{
          this.toStudy(this.ind+1)
        }
      },
      toStudy(part){
        let nameStr = '',conStr='';
        switch (part){
          case 1:
            nameStr = 'PartOne';
            conStr = 'video'
            break;
          case 2:
            nameStr = 'PartTwo'
            conStr = 'book'
            break;
          case 3:
            nameStr = 'PartThree'
            conStr = 'letter'
            break;
          case 4:
            nameStr = 'PartFour'
            conStr = 'song'
            break;
          case 5:
            nameStr = 'PartFive'
            conStr = 'song'
            break;
        }
        this.$router.replace({name:nameStr,query:{id:this.id,part:part}})
      },
      showEject: function (text) {
        this.ejectText = text
        this.ejectStatus = true
      },
      initEject: function () {
        this.ejectStatus = false
      }
    }
  }
</script>

<style scoped lang="less">
  .trans_container {
    width: 100%;
    height: 100vh;
    background: #DAF1FF;
    position: relative;
    .txt{
      width: 100%;
      font-family: FZY4JW--GB1-0;
      font-size: .6rem;
      color: #5D5F5E;
      letter-spacing: .0375rem;
      text-align: center;
      position: absolute;
      bottom: 3.9rem;
      text-align: center;
    }
    .tag{
      width: 9.21rem;
      height: 3.02rem;
      background: url('../../assets/imgs/trans_tag.png');
      background-size: cover;
      margin: auto;
      position: absolute;
      left: calc(~'50% - 4.6rem');
      bottom: .88rem;
      .btn_left{
        width: 2.58rem;
        height: 1.08rem;
        background: url('../../assets/imgs/trans_btn1.png');
        background-size: cover;
        position: absolute;
        bottom: 1.27rem;
        left: 3.25rem;
      }
      .btn_right{
        width: 2.58rem;
        height: 1.08rem;
        background: url('../../assets/imgs/trans_btn2.png');
        background-size: cover;
        position: absolute;
        left: 6.13rem;
        bottom: 1.27rem;
      }
    }
  }
  .hv_tip{
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, .8);
    z-index: 1001;
    font-size: .32rem;
    font-family: PingFangSC-Semibold;
    font-weight: bold;
    color: #FFD356;
    .icon{
      width: 2.04rem;
      height: 1.53rem;
      margin: 4rem auto 0;
      background: url('data:image/png;base64,');
      background-size: cover;
    }
    p{
      padding-top: .4rem;
    }
  }
  /*竖屏*/
  @media screen and (orientation:portrait){
    .hv_tip{
      display: block;
    }
  }
  /*横屏*/
  @media screen and (orientation:landscape){
    .hv_tip{
      display: none;
    }
  }
</style>



