<template>
  <div class="catalog">
    <div class="cata_container" ref="viewBox">
      <ul class="cata_box">
        <li v-for="item in courseArr" @click="toPart(item.id,item.cover)">
          <img :src="item.cover" class="cata_pic">
          <div v-show="item.is_finish" class="tag_finish">已完成</div>
          <p class="cata_txt">{{item.name}}</p>
        </li>
      </ul>
      <div class="cata_ft"></div>
    </div>
    <eject :message="ejectText" :showState="ejectStatus"></eject>
    <div class="hv_tip">
      <div class="icon"></div>
      <p>请旋转至横屏，体验更佳</p>
    </div>
  </div>
</template>
<script scoped>
  import Eject from '../Eject'
  export default {
    name: 'Pay',
    components: {
      eject: Eject,
    },
    data () {
      return {
        cataArr:[],
        courseArr:[],
        nickname:'',
        head_img:'',
        num:2,
        canGroup:true,
        ejectText:'',
        ejectStatus: false,
        count:0,
        scoLeft:0
      }
    },
    created(){
      // this.getInfo()
      // this.shareConfig()
    },
    mounted(){
      this.getCourseList()
      this.shareConfig()
      this.nickname = window.localStorage.getItem('extendAbcNickname')
      this.head_img = window.localStorage.getItem('extendAbcHeadimg');
      this.box = this.$refs.viewBox;
      // 监听这个dom的scroll事件
      this.box.addEventListener('scroll', () => {
        this.scoLeft = this.$refs.viewBox.scrollLeft;
      }, false)
    },
    methods: {
      toPart(id,cover){
        localStorage.setItem('musicalPartCover',cover);
        this.$router.replace({name:'Part',query:{id:id}})
      },
      showEject: function (text) {
        this.ejectText = text
        this.ejectStatus = true
      },
      initEject: function () {
        this.ejectStatus = false
      },
      getCourseList(){
        let member_id = localStorage.getItem('extendAbcMemberid');
        let wUrl = this.$common.config.gzhUrl + "v3/marketingcourse/course/list"
        let fd = this.$common.getParam('get', {course_id:2,member_id: member_id});
        this.$axios.get(wUrl+'?'+fd).then((res) => {
          console.log(res.data)
          if(res.data.code == 200){
            this.courseArr = res.data.data.list
            this.count = res.data.data.unlock_count
            localStorage.setItem('extendAbcLength',this.courseArr.length)
          }
        })
      },
      shareConfig(){
        let member_id = localStorage.getItem('extendAbcMemberid')
        let lUrl = encodeURIComponent(window.location.href)
        let wUrl = this.$common.config.gzhUrl + "v3/wechat/wechat/get-wechat"
        let fd = this.$common.getParam('get', {url: lUrl, member_id: member_id});
        this.$axios.post(wUrl+'?'+fd).then((res) => {
          if(res.data.code == 200){
          let data = res.data.data
          // 微信配置
          wx.config({
            debug: false,
            appId: data.appId,
            timestamp: data.timestamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: ['hideMenuItems'] // 功能列表，我们要使用JS-SDK的什么功能
          });
          wx.ready(function () {
            wx.checkJsApi({
              jsApiList: ['hideMenuItems'],
              success: function(res) {
              }
            });
            wx.hideMenuItems({
              menuList: ["menuItem:share:timeline","menuItem:share:appMessage",]
            });
          });
        }
      })
      }
    }
  }
</script>

<style scoped lang="less">
  .catalog{
    width: 100vw;
    height: 100vh;
    background: url('../../assets/imgs/cata_bg.png') no-repeat;
    background-size: 100% auto;
    overflow: auto;
    position: relative;
    background-color: #A9D832;
    .cata_container {
      overflow-y: hidden;
      z-index: 2;
      position: relative;
      .cata_box{
        width: auto;
        text-align: left;
        font-size: 0;
        white-space: nowrap;
        /*padding:1.62rem 0 0 1.08rem;*/
        /*margin-top: -3.1rem;*/
        margin-top: 1.2rem;
        padding-left: .8rem;
        position: relative;
        z-index: 10;
        li{
          display: inline-block;
          vertical-align: top;
          width: 2.7rem;
          height: 3.2rem;
          background: #FFF;
          border-radius: 1.35rem 1.35rem .3rem .3rem;
          position: relative;
          margin-right: .3rem;
          .cata_pic{
            width: 2.36rem;
            height: 2.36rem;
            border-radius: 50%;
            margin: .17rem .17rem;
          }
          .tag_finish{
            width: 1.08rem;
            height: .48rem;
            background: url('data:image/png;base64,');
            background-size: cover;
            position: absolute;
            left: .76rem;
            top: 2.19rem;
          }
          .cata_txt{
            font-family: FZY4JW--GB1-0;
            font-size: .26rem;
            color: #444444;
            letter-spacing: .006rem;
            text-align: center;
          }
          .lock{
            position: absolute;
            width: 2.04rem;
            height: 2.04rem;
            border-radius: .3rem;
            background: rgba(0,0,0,.2);
            left: .18rem;
            top: .18rem;
            text-align: center;
            .icon{
              display: inline-block;
              width: .38rem;
              height: .5rem;
              background: url('data:image/png;base64,');
              background-size: cover;
              margin: .77rem auto 0;
            }
            p{
              font-family: FZY4JW--GB1-0;
              font-size: .16rem;
              width: 100%;
              color: #FFFFFF;
              letter-spacing: 0;
              text-align: center;
              position: absolute;
              left: 0;
              bottom: 0;
            }
          }
        }
      }
      .cata_ft{
        width: 3.35rem;
        height: 1.8rem;
        background: url('../../assets/imgs/cata_ft.png');
        background-size: cover;
        position: fixed;
        bottom: 0;
        right: .4rem;
      }
    }
    .hv_tip{
      width: 100vw;
      height: 100vh;
      position: fixed;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, .8);
      z-index: 1001;
      font-size: .2rem;
      font-family: PingFangSC-Semibold;
      font-weight: bold;
      color: #FFD356;
      .icon{
        width: 1.02rem;
        height: .76rem;
        margin: 2rem auto 0;
        background: url('data:image/png;base64,');
        background-size: cover;
      }
      p{
        padding-top: .2rem;
      }
    }
    /*竖屏*/
    @media screen and (orientation:portrait){
      .hv_tip{
        display: block;
      }
    }
    /*横屏*/
    @media screen and (orientation:landscape){
      .hv_tip{
        display: none;
      }
    }
  }
</style>



